.wrapper {
  /* width: 95%; */
}
.calculator {
  border: 1px solid #cccccc;
  box-shadow: 10px 10px 30px 0px rgba(0, 0, 0, 0.75);
  border-radius: 20px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -40%);
  min-width: 300px;
  max-width: 400px;
  max-height: 550px;
}
.keys {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  padding: 10px;
  margin: auto;
}
.button {
  height: 50px;
  padding: 5px;
  border-radius: 5px;
  background-color: #fff;
  border: 1px solid #c4c4c4;
  background-color: transparent;
  font-size: 20px;
  color: #4e0707;
}
.button:hover {
  background-color: #e9e2e2;
}
.operator {
  color: aliceblue;
  background-color: #eebb24;
}
.clear {
  background-color: #f0595f;
  border-color: #b0353a;
  color: #fff;
}
.clear:hover {
  background-color: #f17377;
}
.equal-sign {
  background-color: #25a8;
  border-color: #25a8e0;
  color: #fff;
}
.equal-sign:hover {
  background-color: rgba(13, 96, 240, 0.533);
}
